.superposicion {
    font-family: Arial, sans-serif;
    text-align: center;
  }


.superponer {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.8);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
  }
  
  .img-superponer {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    img{
        max-width: 80%;
        max-height: 80%;
    }
  }